import React from 'react';
import {
  Paper,
  Button,
  withStyles,
  Typography,
  Box,
} from '@material-ui/core';

import StarProgressBar from '../../components/StarProgressBar';
import NextButton from '../../components/NextButton';
import ResultCard from '../../components/ResultCard';
import domtoimage from 'dom-to-image';
import jsPDF from 'jspdf';

const styles = {
  content: {
    width: '80vw',
    minWidth: '320px',
    margin: 'auto',
    borderSpacing: 'auto',
    padding: '4vw'
  }
}


class ResultPage extends React.Component {

  onClickDownload() {
    const input = document.getElementById('resultBox');
    const pdf = new jsPDF();
    if (pdf) {
      domtoimage.toPng(input)
        .then(imgData => {
          pdf.addImage(imgData, 'PNG', 0, 0);
          pdf.setPage(3);
          pdf.save('download.pdf');
        });
    }
  }

  render() {
    const { classes } = this.props;

    return (
      <React.Fragment>
        <Paper className={classes.content} elevation={3}>
          <Box>
            <Typography variant="h4" >
              Result
            </Typography>
          </Box>
          <br />
          <Box id="resultBox">
            <ResultCard
              fitCategory="Best Fit"
              score="91"
              field="culinary"
            />
            <br />
            <ResultCard
              fitCategory="Very Good Fit"
              score="84"
              field='engineering'
            />
            <br />
            <ResultCard
              fitCategory="Good Fit"
              score="76"
              field='education'
            />
            <br />
          </Box>
          <br />
          <Button onClick={this.onClickDownload}> Download </Button>
          <NextButton link="/career/end" text="Finish" />
        </Paper>
        <StarProgressBar currStars={4} maxStars={5} />


      </React.Fragment>
    );
  }
}

export default withStyles(styles)(ResultPage);

